module.exports = "<span class=\"hljs-keyword\">import</span> { Component } <span class=\"hljs-keyword\">from</span> <span class=\"hljs-string\">'@angular/core'</span>;\n\n<span class=\"hljs-meta\">@Component</span>({\n  selector: <span class=\"hljs-string\">'recaptcha-demo'</span>,\n  templateUrl: <span class=\"hljs-string\">'./invisible-demo.component.html'</span>,\n})\n<span class=\"hljs-keyword\">export</span> <span class=\"hljs-keyword\">class</span> InvisibleDemoComponent {\n  <span class=\"hljs-keyword\">public</span> captchaResponse: <span class=\"hljs-built_in\">string</span> = <span class=\"hljs-string\">''</span>;\n  <span class=\"hljs-keyword\">public</span> resolved(captchaResponse: <span class=\"hljs-built_in\">string</span>) {\n    <span class=\"hljs-keyword\">const</span> newResponse = captchaResponse\n      ? <span class=\"hljs-string\">`<span class=\"hljs-subst\">${captchaResponse.substr(0, 7)}</span>...<span class=\"hljs-subst\">${captchaResponse.substr(-7)}</span>`</span>\n      : captchaResponse;\n    <span class=\"hljs-keyword\">this</span>.captchaResponse += <span class=\"hljs-string\">`<span class=\"hljs-subst\">${JSON.stringify(newResponse)}</span>\\n`</span>;\n  }\n}\n"